<template>
  <div class="goodlist">
    <!-- <van-search shape="round" placeholder="搜索商品信息" background="#ff6a48" class="vSearch"></van-search> -->
    <div class="headNav">
      <van-grid :border="false">
        <van-grid-item class="red" @click="trunLast">{{$route.query .title}}</van-grid-item>
        <van-grid-item @click=" changePrice " class="changefont">
          价格
          <img src="../assets/hui.png" alt v-if="priceC % 3 == 0" />
          <img src="../assets/bottom.png" alt v-if="priceC % 3 == 1" />
          <img src="../assets/top.png" alt v-if="priceC % 3 == 2" />
        </van-grid-item>
        <van-grid-item @click="changesales">
          销量
          <img src="../assets/hui.png" alt v-if="salesC % 3 == 0" />
          <img src="../assets/bottom.png" alt v-if="salesC % 3 == 1" />
          <img src="../assets/top.png" alt v-if="salesC % 3 == 2" />
        </van-grid-item>
        <van-grid-item>新品</van-grid-item>
      </van-grid>
    </div>
    <div v-if="goods.length!=0">
      <ul class="good">
        <li v-for="i in goods" :key="i.id">
          <router-link :to="`/detail/${i.id}`">
            <img :src="i.image" alt />
          </router-link>
          <div>
            <p>{{ i.store_name }}</p>
            <span>￥{{ i.price }}</span>
            <div>
              <span>
                ￥{{ i.vip_price }}
                <img src="../assets/vip.png" alt />
              </span>
              <span>已售{{ i.sales }}件</span>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <div v-else>
       <Recommend></Recommend>
    </div>
  </div>
</template>
<script>
import Recommend from "@/components/Recommen";
export default {
  name: "GoodList",
  props: ["id"],
  data() {
    return {
      priceC: 0,
      salesC: 0,
      goods: [],
      conditino: {
        page: 1,
        limit: 10,
        keyword: "",
        sid: 0,
        news: 0,
        priceOrder: "",
        salesOrder: "",
      },
    };
  },
   components: {
    Recommend,
  },
  created() {
    this.getProduct();
  },
  methods: {
    trunLast() {
      this.$router.go(-1);
    },
    changePrice() {
      this.priceC++;
      if (this.priceC % 3 == 1) {
        this.conditino.priceOrder = "desc";
      } else if (this.priceC % 3 == 2) {
        this.conditino.priceOrder = "asc";
      } else {
        this.conditino.priceOrder = "";
      }
      this.getProduct();
    },
    changesales() {
      this.salesC++;
      if (this.salesC % 3 == 1) {
        this.conditino.salesOrder = "desc";
      } else if (this.salesC % 3 == 2) {
        this.conditino.salesOrder = "asc";
      } else {
        this.conditino.salesOrder = "";
      }
      this.getProduct();
    },

    getProduct() {
      this.conditino.sid = this.id;
      this.$axios
        .get("api/products", {
          params: this.conditino,
        })
        .then((d) => {
          if (d.data.status == 200) this.goods = d.data.data;
        });
    },
  },
};
</script>
<style lang="less" scoped>
.headNav {
  font-size: 1rem;
  position: relative;
  .red {
    color: red;
  }
  img {
    position: absolute;

    right: 0;
  }
}
.goodlist {
  height: 100vh;
  background-color: #f5f5f5;
  .good {
    width: 96vw;
    margin: 0px 2vw;
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 50px;
    li {
      width: 46vw;
      margin: 1vw 1vw;
      background: white;
      border-radius: 1.5vw;
      overflow: hidden;

      img {
        width: 46vw;
        height: 45vw;
      }
      & > div {
        padding: 2vw 1vw 3vw 1vw;
        width: 44vw;
        p {
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          margin: 0px;
        }
        span {
          color: red;
          font-size: 4vw;
          font-weight: 700;
        }
        div {
          display: flex;
          justify-content: space-between;
          span {
            color: #282828;
            font-size: 3vw;
            img {
              width: 6vw;
              height: 2.3vw;
            }
            &:last-of-type {
              color: #bbb;
              font-weight: 400;
            }
          }
        }
      }
    }
  }
}
</style>